<template>
    <div class="order">
        <van-nav-bar
        title="提交订单"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
        />
        <div class="address-box">
            <van-cell is-link @click="showPopup">选择收货地址</van-cell>
            <div class="user-info">
                <div class="user-box">
                    <div class="user-name">{{userInfo.name}}</div>
                    <div class="user-tel">{{userInfo.tel}}</div>
                </div>
                <div class="user-address">{{userInfo.address}}</div>
            </div>
        </div>
        <van-popup v-model="show" closeable position="bottom" :style="{ height: '42%' }">
            <h3>选择收货地址</h3>
            <van-address-list
            v-model="chosenAddressId"
            :list="list"
            default-tag-text="默认"
            @add="addAddress"
            @select="selectAddress"
            />

        </van-popup>
        <div class="info-box">
            <div class="info-title">订单信息</div>
            <div class="shop-item" v-for="(item,index) in this.shopCarData[0]" :key="index">
                    <div class="shop-item-info">
                        <div class="shop-item-img">
                            <img :src="item.small_img" alt="">
                        </div>
                        <div class="shop-item-desc">
                            <div class="shop-name">
                                <div>{{item.name}}</div> 
                                <div class="condit">{{item.rule}}</div>
                            </div>
                            <div class="ename">{{item.enname}}</div>
                            <div class="shopPrice-box">
                                <div class="price">￥{{item.price}}</div>
                                <div class="count">x{{item.count}}</div>
                            </div>
                        </div>
                    </div>
            </div>
           
            <div class="total">
                <div class="total-count">共计 {{totalCount}} 件商品</div>
                <div class="total-price">合计：￥ <span>{{totalPrice | decimal}}</span> </div>
            </div>
        </div>
        <div class="jiesuan">
            <van-button type="primary" round size="large" color="linear-gradient(to right, #ff6034, #ee0a24)" @click="settleMent">立即结算</van-button>
        </div>
    </div>
</template>

<script>
    import '../assets/less/order.less'
import ManageAddressVue from './ManageAddress.vue';

    export default {
        name: 'Order',
        data() {
            return {
                show: false,
                // 购物车数据集合
                sids: '',
                //需要购买的商品数据
                shopCarData: [],
                chosenAddressId: '1',
                totalCount: '',
                totalPrice: 0,
                list: [],
                userInfo: {}
            };
        },
        created(){
            
            this.sids = this.$route.query.sids.split('-');
            
            this.commitShopCar();
            this.findAddress();
            //选中默认地址
            this.selectDefaultAddress();
        },
        methods: {
            onClickLeft(){
                this.$router.go(-1);
            },
            showPopup(){
                this.show = true;
            },
            commitShopCar(){
                let tokenString = localStorage.getItem('token');
                if(!tokenString){
                    this.$toast("请登录");
                    return this.$router.push({name:'Login'});
                }
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: "GET",
                    url: '/commitShopcart',
                    params:{
                        appkey: this.appkey,
                        tokenString,
                        sids: JSON.stringify(this.sids)
                    }
                }).then(res => {
                    this.$toast.clear();
                    if(res.data.code == 50000){
                        this.shopCarData.push(res.data.result);
                        this.shopCarData[0].map(v => {
                            this.totalCount += v.count;
                            this.totalPrice = parseInt(this.totalPrice) + parseInt(v.price) * parseInt(v.count );
                        })
                        
                    }


                }).catch(err => {
                    this.$toast.clear();
                    
                })
            },
            addAddress(){
                this.$router.push({name:'NewAddress'})
            },
            findAddress(){
                let tokenString = localStorage.getItem('token');
                if(!tokenString){
                    this.$toast("请登录");
                    return this.$router.push({name:'Login'});
                }
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0,
                });
                this.axios({
                    method: 'GET',
                    url: '/findAddress',
                    params: {
                        appkey: this.appkey,
                        tokenString,
                    }
                }).then(res => {
                    this.$toast.clear();
                    if(res.data.code == 20000){
                        res.data.result.map((v,index) => {
                            v.address = `${v.province}${v.city}${v.county}${v.addressDetail}`
                            this.list.push(v);                          
                        })
                    }
                }).catch(err => {
                    this.$toast.clear();
                })
            },
            selectAddress(item){
                for(let key in item){
                    item.address = `${item.province}${item.city}${item.county}${item.addressDetail}`;
                    this.userInfo[key] = item[key];
                }
            },
            selectDefaultAddress(){
                setTimeout(() => {          
                   this.list.map(v => {
                       if(v.isDefault){
                           this.userInfo = v;
                       }
                   }) 
                }, 500);          
            },
            // 立即结算
            settleMent(){
                let tokenString = localStorage.getItem('token');
                if(!tokenString){
                    this.$toast("请登录");
                    return this.$router.push({name:'Login'});
                }
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0,
                });
                this.axios({
                    method: 'POST',
                    url: '/pay',
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        sids: JSON.stringify(this.sids),
                        phone: this.userInfo.tel,
                        address: this.userInfo.address,
                        receiver: this.userInfo.name
                    }
                }).then(res => {
                    this.$toast.clear();
                    if(res.data.code == 700){
                        this.$toast("请登录");
                        this.$router.push({name: 'Login'});
                    }else if(res.data.code == 60000){
                        setTimeout(() => {
                            this.$router.push({name: 'Pay'});
                        })
                    }
                }).catch(err => {
                    this.$toast.clear();
                }) 
            }
        },
    }
</script>